// Prevent clickable elements from being covered by backgrounds
a, .menu__item, #effects-toggle {
  z-index: 2;
}

#background-effects {
  opacity: 0;
  transition: 2s opacity;

  &.background-effects_minify {
    .background-container-3, .background-container-4 {
      display: none;
    }
  }

  &.background-effects_active {
    transition: 2s opacity;
    opacity: 0.85;
  }

  > div {
    position: absolute;
    border-radius: 50%;
    z-index: 1;
    filter: blur(21rem);
  }
}

